
"use client";

import Map, { Marker } from "react-map-gl";
import 'mapbox-gl/dist/mapbox-gl.css';
import { useEffect, useRef } from "react";
import type { MapRef } from "react-map-gl";
import { useTrackingStore } from "@/store/tracking-store";
import { mockVehicles } from "@/lib/mock-data";
import VehicleMarker from "./vehicle-marker";
import RouteLayer from "./route-layer";

export default function TrackingMap() {
  const { vehicles, setVehicles, autoRefresh, selectedVehicle } = useTrackingStore();
  const mapRef = useRef<MapRef>(null);

  useEffect(() => {
    // Initial load
    setVehicles(mockVehicles);

    if (!autoRefresh) return;
    
    // Simulate GPS push every 15 seconds
    const interval = setInterval(() => {
        setVehicles(currentVehicles => currentVehicles.map(v => ({
            ...v,
            // a little bit of random movement
            lat: v.lat + (Math.random() - 0.5) * 0.002,
            lng: v.lng + (Math.random() - 0.5) * 0.002,
        }))); 
    }, 15000);

    return () => clearInterval(interval);
  }, [autoRefresh, setVehicles]);
  
  useEffect(() => {
    if (selectedVehicle && mapRef.current) {
        mapRef.current.flyTo({
            center: [selectedVehicle.lng, selectedVehicle.lat],
            zoom: 14,
            duration: 2000
        });
    }
  }, [selectedVehicle]);

  const mapboxToken = process.env.NEXT_PUBLIC_MAPBOX_TOKEN;

  if (!mapboxToken) {
    return (
      <div className="flex h-full w-full items-center justify-center bg-muted">
        <div className="text-center">
            <h3 className="text-lg font-semibold">Mapbox Token Missing</h3>
            <p className="text-sm text-muted-foreground">
                Please add NEXT_PUBLIC_MAPBOX_TOKEN to your .env file.
            </p>
        </div>
      </div>
    )
  }

  return (
    <Map
      ref={mapRef}
      mapboxAccessToken={mapboxToken}
      initialViewState={{ latitude: 24.7136, longitude: 46.6753, zoom: 10 }}
      mapStyle="mapbox://styles/mapbox/light-v11"
    >
      {vehicles.map((v) => (
        <Marker key={v.id} latitude={v.lat} longitude={v.lng}>
          <VehicleMarker vehicle={v} />
        </Marker>
      ))}
      {selectedVehicle && selectedVehicle.route && (
        <RouteLayer coordinates={selectedVehicle.route} />
      )}
    </Map>
  );
}
